console.log('index.js !!!');

import Modal from '../../js/modal'
import Alerts from '../../js/alerts'
import Header from '../../js/header'
import Select from '../../js/select-new'
import cardToggle from '../../js/cardToggle'

import '../../js/cardTab'
import '../../js/closeHelper'
import '../../js/clearInput'

$(document).ready(function () {
  Modal().init();
  Select().init();
  Alerts().init();
  Header().init();
  cardToggle().init();
  $('.card__head--toggle').on('click', function () {
    var arrow = $(this).parent().find('.card__arrow');
    var inner = $(this).parent().find('.card__inner--hint');
    inner.toggle();
    arrow.toggleClass('opened');
  });
});





