import PerfectScrollbar from 'perfect-scrollbar'


$(document).ready(function(){
  if($('.js-card-tab__tabs-wrap').length > 0){
    const ps = new PerfectScrollbar('.js-card-tab__tabs-wrap', {
      suppressScrollX: false,
      suppressScrollY: true,
    });
  }
});




